.overview{padding:80px 0}.content .row{gap:24px 0}.card{background:var(--white);border-radius:12px;padding:32px 24px;height:100%;display:flex;flex-direction:column;transition:all .3s ease;border:1px solid rgba(26,80,119,.1)}.card:hover{transform:translateY(-4px);box-shadow:0 8px 30px rgba(0,0,0,.15);border-color:var(--primary-light)}.cardHeader{margin-bottom:24px}.cardIcon{width:56px;height:56px;border-radius:50%;background:linear-gradient(135deg, var(--primary), var(--primary-light));display:flex;align-items:center;justify-content:center;color:var(--white);font-weight:700;font-size:20px;margin:0 auto}.cardBody{flex:1;text-align:center;margin-bottom:24px}.cardTitle{font-size:20px;font-weight:700;color:var(--primary);margin-bottom:16px;line-height:1.3;min-height:52px;display:flex;align-items:center;justify-content:center;text-align:center}.cardDescription{color:var(--text-secondary);line-height:1.6;font-size:15px}.cardFooter{text-align:center}.readMoreBtn{display:inline-flex;align-items:center;gap:8px;padding:12px 24px;font-weight:600;transition:all .3s ease}.readMoreBtn::after{content:"→";transition:transform .3s ease}.readMoreBtn:hover::after{transform:translateX(4px)}@media(max-width: 991px){.overview{padding:60px 0}.card{margin-bottom:24px}.cardTitle{font-size:18px;min-height:auto}}@media(max-width: 767px){.overview{padding:40px 0}.card{padding:24px 20px}.cardIcon{width:48px;height:48px;font-size:18px}.cardTitle{font-size:16px}.cardDescription{font-size:14px}}